// @mui
import { Box, Card, Container, Typography } from '@mui/material';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Markdown from '../../components/Markdown';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { HOST_API, } from '../../config';
// sections
// hooks
import useAuth from '../../hooks/useAuth';
import CableID from './cableid';
// ----------------------------------------------------------------------
export default function DocsIUC() {
  const { themeStretch } = useSettings();

  const {setting} = useAuth();

  return (
    <Page title="Docs">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="IUC Verification API"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Home' },
          ]}
        />

       
          <Card>

            <Box sx={{ p: { xs: 3, md: 5 } }}>
              <Typography  sx={{ mb: 5 }}>
              This section contains the recommended flow for integrating  IUC Verification services on the {setting?.general?.app_name} RESTful API.
                           </Typography>

        
                <Markdown children={`<hr> <h3>Available Endpoints</h3><br/> <p>To integrate the ${setting?.general?.app_name} IUC Verification Payment RESTful API, the endpoints below applies:</p>
                <p><br></p><p>
                <ol> <li>iuc</li> <li>cable</li>  </ol>
                
                `} />
                <CableID />
           <Markdown children={`<hr> <h3>VERIFY IUC NUMBER</h3><br/> <p>Using a GET method,  IUC Verification bundle can with the endpoint below:</p>
                <p><br></p><p>
                <hr> <h3>Enpoint URL</h3><br/> <blockquote>${HOST_API}/api/cable/cable-validation?iuc=12345555&cable=1</blockquote><br>
                <hr> <h3>Expected Response</h3><br/>
                <pre class="ql-syntax" spellcheck="false">
                {
                    "status": "success",
                    "name": "ADEX DEVELOPER"
                }
                </pre>
                
                `} />
            </Box>
          </Card>
      </Container>
    </Page>
  );
}
